.App {
  text-align: center;
  margin-top: 50px;
}

.add-row {
  height: 30px;
  width: 100px;
  color: white;
  background-color: #00acdc;
  border: none;
  outline: none;
  border-radius: 3px;
  transition: background-color 0.5s;

  &:hover {
    background-color: blue;
    cursor: pointer;
  }
}
